export function initScrollUp() {
  const scrollUpBtn = document.getElementById("scrollUpBtn");

  // DOM Guard: Buton yoksa dur
  if (!scrollUpBtn) {
    console.warn("Scroll button bulunamadÄ±!"); // Konsolda bunu gÃ¶rÃ¼rsen HTML'de buton yok demektir.
    return;
  }

  window.addEventListener("scroll", () => {
    // 300px aÅŸaÄŸÄ± inildi mi?
    if (window.scrollY > 300) {
      scrollUpBtn.classList.add("show"); // CSS'teki .show class'Ä±nÄ± ekler
    } else {
      scrollUpBtn.classList.remove("show");
    }
  });

  scrollUpBtn.addEventListener("click", () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  });
}